<template>
  <div>
    <el-row>
      <el-col v-if="items.operats" :span="operatCol" :xs="24">
        <!-- 全选框 -->
        <el-checkbox
          v-if="items.titles.choose"
          v-model="checkAll"
          @change="checkall()"
          label="全选"
          border
          size="small"
          style="margin-right: 5px;"
        ></el-checkbox>
        <!-- 操作按钮组 -->
        <el-button-group>
          <template v-for="v in items.operats">
            <el-button
              :key="v.text"
              size="small"
              :type="operatType(v.action)"
              :icon="operatIcon(v.action)"
              @click="operat(v.action)"
            >
              {{ v.text }}
            </el-button>
          </template>
        </el-button-group>
      </el-col>
      <el-col
        v-if="items.pages"
        :span="pageCol"
        :xs="24"
        style="text-align: right;"
      >
        <!-- 分页 -->
        <el-pagination
          background
          layout="prev, pager, next"
          :page-count="items.pages.total"
          :current-page="Number(items.pages.current)"
          @current-change="queryPage"
        >
        </el-pagination>
      </el-col>
    </el-row>

    <el-table
      :data="items.results"
      style="width: 100%;"
      :max-height="tableMaxHeight"
      stripe
      :show-summary="items.stats ? true : false"
      :summary-method="getSum"
    >
      <template v-for="(title, key) in items.titles">
        <el-table-column
          v-if="key == 'choose'"
          :key="key"
          width="50"
          :label="title"
          fixed="left"
          align="center"
        >
          <!--选择-->
          <template slot-scope="scope">
            <el-checkbox v-model="scope.row[key].checked"></el-checkbox>
          </template>
        </el-table-column>
        <el-table-column
          v-else-if="key == 'operat'"
          :key="key"
          width="80"
          :label="title"
          fixed="right"
          align="center"
        >
          <!--操作-->
          <template slot-scope="scope">
            <el-tooltip
              v-if="scope.row.operat.edit"
              content="编辑"
              effect="light"
            >
              <el-button
                size="mini"
                icon="el-icon-edit-outline"
                @click="edit(scope.row.pk)"
                circle
              ></el-button>
            </el-tooltip>
            <el-tooltip
              v-if="scope.row.operat.copy"
              content="复制"
              effect="light"
            >
              <el-button
                size="mini"
                icon="el-icon-document-copy"
                @click="copy(scope.row.pk)"
                style="margin-left: 1px;"
                circle
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
        <!-- 简单模式 -->
        <el-table-column
          v-else-if="typeof title == 'string'"
          :key="key"
          :label="title"
        >
          <span slot-scope="scope">{{ scope.row[key] }}</span>
        </el-table-column>
        <el-table-column
          v-else
          :key="key + title.ord + querys.hidden.ord.value"
          :min-width="title.width"
          :align="title.isStat ? 'right' : 'left'"
          :fixed="title.isStat ? 'right' : false"
          :header-align="title.isStat ? 'center' : 'left'"
        >
          <!--表头-->
          <template slot="header">
            <el-tooltip
              :content="'点击' + (title.ord == 'd' ? '倒序' : '顺序') + '排序'"
              effect="light"
            >
              <a @click="resultQuery('ord', title.key + '-' + title.ord)">
                {{ title.text }}
                <i
                  v-if="orderKey == title.key"
                  :class="
                    orderOrd == 'd'
                      ? 'el-icon-caret-bottom'
                      : 'el-icon-caret-top'
                  "
                ></i>
              </a>
            </el-tooltip>
          </template>
          <!--内容-->
          <template slot-scope="scope">
            <!-- 直接输出 -->
            <template v-if="typeof scope.row[key] == 'string'">
              {{ scope.row[key] }}
            </template>

            <!-- input -->
            <el-input
              v-else-if="scope.row[key].html == 'input'"
              v-model="scope.row[key].value"
              size="small"
              @change="scope.row.choose.checked = true"
            ></el-input>

            <!-- textarea -->
            <el-input
              v-else-if="scope.row[key].html == 'textarea'"
              v-model="scope.row[key].value"
              type="textarea"
              :rows="2"
              size="small"
              @change="scope.row.choose.checked = true"
            ></el-input>

            <!-- select -->
            <el-select
              v-else-if="scope.row[key].html == 'select'"
              v-model="scope.row[key].value"
              :multiple="scope.row[key].multiple"
              :collapse-tags="scope.row[key].multiple"
              filterable
              size="small"
              @change="scope.row.choose.checked = true"
            >
              <el-option
                v-for="(name, key) in scope.row[key].option"
                :key="key"
                :label="name"
                :value="key"
              ></el-option>
            </el-select>

            <!-- 数组 -->
            <template v-else-if="scope.row[key] instanceof Array">
              <template v-for="v in scope.row[key]">
                <el-tooltip
                  :key="v.text"
                  :content="v.title"
                  placement="top"
                  effect="light"
                >
                  <el-tag v-if="v.color" :type="v.color">{{ v.text }}</el-tag>
                  <span v-else style="margin-right: 5px;">{{ v.text }}</span>
                </el-tooltip>
              </template>
            </template>

            <!-- span tag -->
            <el-tooltip
              v-else-if="scope.row[key].html == 'span'"
              :content="scope.row[key].title"
              placement="top"
              effect="light"
            >
              <el-tag v-if="scope.row[key].color" :type="scope.row[key].color">
                {{ scope.row[key].text }}
              </el-tag>
              <span v-else>{{ scope.row[key].text }}</span>
            </el-tooltip>

            <!-- 图标 -->
            <i
              v-else-if="scope.row[key].html == 'icon'"
              :class="scope.row[key].class"
            ></i>

            <!-- 图片 -->
            <el-image
              v-else-if="scope.row[key].html == 'img'"
              style="width: 100px; height: 100px"
              fit="contain"
              :src="scope.row[key].src"
              :preview-src-list="srcList(key)"
            ></el-image>

            <!-- 链接 -->
            <el-link
              v-else-if="scope.row[key].html == 'a'"
              type="primary"
              :href="'#/index/' + scope.row[key].href"
              target="_blank"
            >
              {{ scope.row[key].text }}
            </el-link>

            <template v-else>{{ scope.row[key] }}</template>
          </template>
        </el-table-column>
      </template>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "ResultTable",
  data: function() {
    return {
      checkAll: false
    };
  },
  props: {
    items: Object,
    querys: Object,
    query: Function,
    edit: Function,
    copy: Function,
    resultOperat: Function,
    api: String
  },
  methods: {
    queryPage(page) {
      this.resultQuery("page", page);
    },
    resultQuery(name, value) {
      this.querys.hidden[name].value = value;
      this.query(false, false);
    },
    checkall() {
      this.items.results.forEach(item => {
        item.choose.checked = this.checkAll;
      });
    },
    operat(action) {
      var checkedStr = "";
      // 检查需要选取记录的操作
      if (action != "export" && action != "cache") {
        var hasChecked = false;
        this.items.results.forEach(item => {
          if (item.choose.checked) {
            hasChecked = true;
            return;
          }
        });
        if (!hasChecked) {
          this.$alert("没有记录被选取", "错误", { type: "error" });
          return;
        }
        this.items.results.forEach(item => {
          if (item.choose.checked) {
            checkedStr += item.choose.name + "=" + item.choose.value + "&";
          }
        });
      }
      if (action == "deletes") {
        var vue = this;
        this.$confirm("删除后将无法恢复，确认继续？", "提示")
          .then(function() {
            vue.resultOperat(action, checkedStr);
          })
          .catch(function() {});
      } else if (action == "export") {
        var formData = this.query(true, true);
        // @todo 需要修改
        var url = this.api + "export&" + formData;
        window.open(url, "_blank");
      } else {
        if (action == "saves") {
          this.items.results.forEach(item => {
            if (item.choose.checked) {
              for (var k in item) {
                if (k != "choose" && item[k].name) {
                  checkedStr += item[k].name + "=" + item[k].value + "&";
                }
              }
            }
          });
        }
        this.resultOperat(action, checkedStr);
      }
      this.checkAll = false;
    },
    operatType(action) {
      switch (action) {
        case "export":
          return "success";
        case "cache":
          return "warning";
        case "deletes":
          return "danger";
        case "saves":
          return "primary";
        default:
          return "info";
      }
    },
    operatIcon(action) {
      switch (action) {
        case "deletes":
          return "el-icon-delete";
        case "export":
          return "el-icon-document";
        case "saves":
          return "el-icon-circle-check";
        case "cache":
          return "el-icon-refresh";
        default:
          return "el-icon-s-operation";
      }
    },
    srcList(key) {
      var list = [];
      this.items.results.forEach(item => {
        list.push(item[key].src);
      });
      return list;
    },
    getSum() {
      var sum = [];
      if (!this.items.stats) {
        return sum;
      }
      var i = 0;
      for (var key in this.items.titles) {
        if (i === 0) {
          sum.push("总计");
        } else if (this.items.stats[key]) {
          sum.push(this.items.stats[key]);
        } else {
          sum.push("");
        }
        i++;
      }
      return sum;
    }
  },
  computed: {
    orderKey: function() {
      if (this.querys.hidden.ord.value) {
        var temp = this.querys.hidden.ord.value.split("-");
        return temp[0];
      }
      return "";
    },
    orderOrd: function() {
      if (this.querys.hidden.ord.value) {
        var temp = this.querys.hidden.ord.value.split("-");
        return temp[1];
      }
      return "";
    },
    pageCol: function() {
      if (this.items.pages.total > 5) {
        return 10;
      }
      return this.items.pages.total + 2;
    },
    operatCol: function() {
      return 24 - this.pageCol;
    },
    tableMaxHeight: function() {
      return window.innerHeight - 160 + "px";
    }
  }
};
</script>
