<template>
  <div>
    <template v-for="(item, key) in items">
      <!--隐藏-->
      <template v-if="item.data.type == 'hidden'"></template>
      <el-form-item
        v-else
        :key="item.data.name"
        :label="item.title.text"
        :required="item.data.require"
      >
        <el-tooltip
          v-if="item.title.notice"
          :content="item.title.notice"
          slot="label"
          effect="light"
        >
          <span>{{ item.title.text }}</span>
        </el-tooltip>
        <!--普通输入-->
        <el-input
          v-if="item.data.type == 'text'"
          v-model="item.data.value"
          clearable
        ></el-input>
        <!--密码-->
        <el-input
          v-else-if="item.data.type == 'password'"
          v-model="item.data.value"
          show-password
          clearable
        ></el-input>
        <!--文本区域-->
        <el-input
          v-else-if="item.data.html == 'textarea'"
          type="textarea"
          :rows="item.data.rows"
          v-model="item.data.value"
          clearable
        ></el-input>
        <!--上传文件-->
        <el-upload
          v-else-if="item.data.type == 'file'"
          :auto-upload="false"
          :name="item.data.key"
          action=""
        >
          <el-button slot="trigger" type="primary">选取文件</el-button>
        </el-upload>
        <!--日期-->
        <el-date-picker
          v-else-if="item.data.type == 'date'"
          v-model="item.data.value"
          value-format="yyyy-MM-dd"
          type="date"
          clearable
        ></el-date-picker>
        <!--数字-->
        <el-input-number
          v-else-if="item.data.type == 'number'"
          v-model="item.data.value"
        ></el-input-number>
        <!--select-->
        <el-select
          v-else-if="item.data.html == 'select'"
          v-model="item.data.value"
          :multiple="item.data.multiple"
          :collapse-tags="item.data.multiple"
        >
          <el-option
            v-for="(name, k) in item.data.option"
            :key="k"
            :label="name"
            :value="k + ''"
          ></el-option>
        </el-select>
        <!--radio-->
        <el-radio-group
          v-else-if="item.data.html == 'radio'"
          v-model="item.data.value"
        >
          <el-radio-button
            v-for="(v, k) in item.data.option"
            :key="k"
            :label="v"
          ></el-radio-button>
        </el-radio-group>
        <!--checkbox-->
        <el-checkbox-group
          v-else-if="item.data.html == 'checkbox'"
          v-model="item.data.value"
        >
          <el-checkbox-button
            v-for="(v, k) in item.data.option"
            :label="v"
            :key="k"
          ></el-checkbox-button>
        </el-checkbox-group>
        <el-tree
          v-else-if="item.data.html == 'tree'"
          :data="item.data.data"
          :props="{ label: 'name', children: 'submenu' }"
          show-checkbox
          node-key="id"
          :ref="'tree' + key"
          default-expand-all
          check-on-click-node
          check-strictly
          @check-change="treeCheckChange(key)"
          :default-checked-keys="item.data.value"
        ></el-tree>
        <!--直接显示-->
        <span v-else>{{ item.data.text }}</span>
      </el-form-item>
    </template>
  </div>
</template>

<script>
export default {
  name: "Query",
  props: {
    items: Array
  },
  methods: {
    treeCheckChange(key) {
      this.items[key].data.value = this.$refs["tree" + key][0].getCheckedKeys();
    },
    getTrees(data) {
      var ary = [];
      for (var i in data) {
        if (data[i].submenu) {
          ary.push(data[i]);
          ary.push.apply(ary, this.getTrees(data[i].submenu));
        } else {
          ary.push(data[i]);
        }
      }
      return ary;
    }
  }
};
</script>
