<template>
  <div
    class="el-input el-input--medium el-input-group el-input-group--append el-input-group--prepend el-input--suffix"
  >
    <div class="el-input-group__prepend">{{ item.title }}</div>
    <el-select
      style="width: 100%;"
      v-model="item.query.value"
      placeholder="请选择"
      :multiple="item.query.multiple"
      :collapse-tags="item.query.multiple"
      filterable
      size="medium"
    >
      <el-option
        v-for="(name, key) in item.query.option"
        :key="key"
        :label="name"
        :value="key"
      ></el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  name: "QuerySelect",
  props: {
    item: Object
  }
};
</script>
