<template>
  <el-container>
    <el-aside
      :width="asideWidth"
      style="background-color: #2f4050;min-height: 100vh;"
    >
      <el-menu
        :collapse="isMenuCollapse"
        :collapse-transition="false"
        background-color="#2f4050"
        text-color="#a7b1c2"
        active-text-color="#ffffff"
        :router="true"
        :default-openeds="['0']"
      >
        <submenu :items="data.menu" :isMenuCollapse="isMenuCollapse"></submenu>
      </el-menu>
    </el-aside>

    <el-container>
      <el-header
        height="50px"
        style="background-color: #f3f3f4;padding: 10px 20px;"
      >
        <el-row>
          <el-col :span="12">
            <span style="font-size: 22px;letter-spacing: 5px;">
              {{ data.title }}
            </span>
          </el-col>
          <el-col :span="12" style="text-align: right;">
            <el-tooltip content="展开或收起菜单">
              <el-button
                icon="el-icon-s-grid"
                @click="menuCollapse"
                size="small"
                type="success"
                circle
              ></el-button>
            </el-tooltip>
            <el-tooltip content="注销">
              <el-button
                type="danger"
                icon="el-icon-switch-button"
                size="small"
                circle
                style="margin-left: 5px;"
              ></el-button>
            </el-tooltip>
          </el-col>
        </el-row>
      </el-header>

      <el-main style="padding-top: 0px;padding-bottom: 0px;">
        <el-tabs v-model="activeTab">
          <el-tab-pane v-if="data.tabs.result" name="result">
            <span slot="label"><i class="el-icon-tickets"></i> 结果</span>
            <result-table
              v-if="data.tabs.result.titles"
              :items="data.tabs.result"
              :querys="data.tabs.query"
              :query="query"
              :edit="edit"
              :copy="copy"
              :resultOperat="resultOperat"
              :api="api"
            ></result-table>
          </el-tab-pane>

          <el-tab-pane v-if="data.tabs.query" name="query">
            <span slot="label"><i class="el-icon-search"></i> 搜索</span>
            <form @submit.prevent="query(true, false)">
              <query :items="data.tabs.query"></query>
            </form>
          </el-tab-pane>

          <el-tab-pane v-if="data.tabs.add" name="add">
            <span slot="label"><i class="el-icon-plus"></i> 添加</span>
            <el-form
              @submit.native.prevent="add('add', 0)"
              size="medium"
              label-width="100px"
              label-position="right"
            >
              <add :items="data.tabs.add"></add>
              <el-form-item>
                <el-button type="primary" native-type="submit">保存</el-button>
                <el-button type="success" @click="add('add', 1)">
                  保存并继续添加
                </el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>

          <el-tab-pane v-if="data.tabs.edit" name="edit">
            <span slot="label"><i class="el-icon-edit-outline"></i> 编辑</span>
            <el-form
              @submit.native.prevent="add('edit', 0)"
              size="medium"
              label-width="100px"
              label-position="right"
            >
              <add :items="data.tabs.edit"></add>
              <el-form-item>
                <el-button type="primary" native-type="submit">保存</el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>

          <el-tab-pane v-if="data.tabs.import" name="import">
            <span slot="label"><i class="el-icon-upload2"></i> 导入</span>
            <div>
              <span>
                上传文件格式必须是<el-tag>.xls</el-tag>/<el-tag>.xlsx</el-tag>/<el-tag>.csv</el-tag>，
              </span>
              <el-link
                v-if="data.tabs.import.template"
                :href="data.tabs.import.template"
                target="_blank"
                type="primary"
              >下载模板</el-link>
              <span v-else>请以模块导出的文件为模版</span>
              <el-divider></el-divider>
              <span>第一行为表头，列的顺序可以随意</span>
              <el-divider></el-divider>
              <span>如果是<el-tag>修改</el-tag>记录，保留<el-tag>编号</el-tag>列</span>
              <el-divider></el-divider>
              <span>如果是<el-tag>添加</el-tag>记录，去掉<el-tag>编号</el-tag>列</span>
              <el-divider></el-divider>
              <span>注意<el-tag>日期</el-tag>一定要用<el-tag>文本</el-tag>格式</span>
              <el-divider></el-divider>
              <el-upload
                :action="api + 'import'"
                :name="data.tabs.import.name"
                with-credentials
                :show-file-list="false"
                :on-success="parsApiReturn"
                :on-error="parsApiReturn"
                :before-upload="mpfImport"
              >
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </div>
          </el-tab-pane>

          <el-tab-pane v-if="data.tabs.explain" name="explain">
            <span slot="label"><i class="el-icon-info"></i> 说明</span>
            <div v-for="(item, index) in data.tabs.explain" :key="index">
              <span><span v-html="item"></span></span>
              <el-divider></el-divider>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-main>

      <!--el-footer height="22px" style="background-color: #f3f3f4;text-align: center;">
        <small>©MPF Author ChenLiang</small>
      </el-footer-->
    </el-container>
  </el-container>
</template>

<script>
import Query from "@/components/Query.vue";
import ResultTable from "@/components/result/Table.vue";
import Add from "@/components/Add.vue";
import Submenu from "@/components/Submenu.vue";

export default {
  name: "Index",
  data() {
    return {
      loading: null,
      activeTab: "result",
      ctr: "",
      api: "",
      isMenuCollapse: false,
      asideWidth: "200px",
      data: {
        title: "",
        menu: [],
        tabs: {}
      }
    };
  },
  mounted: function() {
    this.ctr = this.$route.params.ctr;
  },
  methods: {
    menuCollapse() {
      this.isMenuCollapse = !this.isMenuCollapse;
      this.asideWidth = this.isMenuCollapse ? "65px" : "200px";
    },
    query: function(isSearch, isGet) {
      if (isSearch) {
        this.data.tabs.query.hidden.page.value = 1;
      }
      var temp,
        formData = "";
      this.data.tabs.query.data.forEach(item => {
        if (typeof item.query.value == "object") {
          for (var k in item.query.value) {
            formData += item.query.name + "=" + item.query.value[k] + "&";
          }
        } else {
          temp = (item.query.value + "").trim();
          if (temp !== "false" && temp.length > 0) {
            formData += item.query.name + "=" + temp + "&";
          }
          if (item.query2) {
            temp = (item.query2.value + "").trim();
            if (temp !== "false" && temp.length > 0) {
              formData += item.query2.name + "=" + temp + "&";
            }
          }
        }
      });
      this.data.tabs.query.display.forEach(item => {
        if (item.checked) {
          formData += item.name + "=" + item.value + "&";
        }
      });
      for (var k in this.data.tabs.query.hidden) {
        formData +=
          this.data.tabs.query.hidden[k].name +
          "=" +
          this.data.tabs.query.hidden[k].value +
          "&";
      }
      if (isGet) {
        return formData;
      }
      this.queryApi(this.api + "result", formData);
    },
    add: function(form, state) {
      var msg = "",
        formData = "";
      if (form == "add") {
        this.data.tabs[form][0].data.value = state;
      }
      this.data.tabs[form].forEach(item => {
        if (item.data.name) {
          if (item.data.require) {
            if (
              (typeof item.data.value == "object" &&
                item.data.value.length == 0) ||
              (item.data.value + "").trim().length == 0
            ) {
              msg += item.title.text + "不能为空<br/>";
            }
          }
          if (typeof item.data.value == "object") {
            for (var k in item.data.value) {
              formData += item.data.name + "=" + item.data.value[k] + "&";
            }
          } else {
            formData += item.data.name + "=" + item.data.value + "&";
          }
        }
      });
      if (msg) {
        this.$alert(msg, "错误", {
          type: "error",
          dangerouslyUseHTMLString: true
        });
        return;
      }
      this.queryApi(this.api + "save", formData);
    },
    resultOperat: function(action, formData) {
      this.queryApi(this.api + action, formData);
    },
    edit: function(pk) {
      this.queryApi(this.api + "edit", "pk=" + pk);
    },
    copy: function(pk) {
      this.queryApi(this.api + "copy", "pk=" + pk);
    },
    mpfImport: function() {
      this.loading = this.$loading();
      return true;
    },
    queryApi: function(url, data) {
      this.loading = this.$loading();
      var vue = this;
      var config = {
        headers: { "Content-Type": "application/x-www-form-urlencoded" }
      };
      if (typeof data == "object") {
        var str = "";
        for (var i in data) {
          str += i + "=" + data[i] + "&";
        }
        data = str;
      }
      this.axios
        .post(url, data, config)
        .then(function(res) {
          vue.parsApiReturn(res.data);
        })
        .catch(function() {
          vue.loading.close();
          vue.$alert("请求服务器失败", "错误", { type: "error" });
        });
    },
    // 解析API返回的数据
    parsApiReturn: function(data) {
      this.loading.close();
      // 服务器错误
      if (typeof data == "string") {
        this.$alert(data.replace(/\n/g, "<br/>"), "错误", {
          type: "error",
          dangerouslyUseHTMLString: true
        });
        return;
      }
      switch (data.code) {
        case 0: // 失败
          this.$alert(data.msg.replace(/\n/g, "<br/>"), "错误", {
            type: "error",
            dangerouslyUseHTMLString: true
          });
          break;
        case 1: // 加载页面
          if (data.target == "index") {
            this.data = data.data;
            this.activeTab = Object.keys(this.data.tabs.result).length
              ? "result"
              : "query";
          } else {
            this.$set(this.data.tabs, data.target, data.data);
            this.activeTab = data.target;
          }
          if (data.msg) {
            this.$message({
              message: data.msg,
              type: "warning"
            });
          }
          break;
        case 2: // 跳转到登陆
          break;
        case 3: // 提示后刷新结果页
          var vue = this;
          this.$alert(data.msg, "", {
            type: "success",
            callback: function() {
              vue.query(false, false);
            }
          });
          break;
        case 4: // debug
          this.$alert(data.data, "调试信息", {
            type: "info"
          });
          break;
        case 5: // 提示成功信息
          this.$message({
            message: data.msg,
            type: "success",
            offset: 200
          });
          break;
      }
    }
  },
  components: {
    Query,
    ResultTable,
    Add,
    Submenu
  },
  watch: {
    $route() {
      this.ctr = this.$route.params.ctr;
    },
    ctr() {
      this.api = "/mpf/api.php?ctr=" + this.ctr + "&act=";
      this.queryApi(this.api + "index", this.$route.query);
    }
  }
};
</script>

<style>
body {
  margin: 0px;
}
</style>
