<template>
  <div>
    <el-row>
      <el-col
        v-for="item in items.data"
        :key="item.key"
        :xl="5"
        :lg="7"
        :md="7"
        :sm="11"
        :xs="23"
        style="margin-bottom: 15px;margin-right: 10px;"
      >
        <query-text v-if="item.type == 'text'" :item="item"></query-text>
        <query-number
          v-if="item.type == 'number' || item.type == 'stat'"
          :item="item"
        ></query-number>
        <query-select
          v-if="item.type == 'select' || item.type == 'selectone'"
          :item="item"
        ></query-select>
        <query-date v-if="item.type == 'date'" :item="item"></query-date>
      </el-col>
    </el-row>

    <el-row>
      <div
        class="el-input el-input--medium el-input-group el-input-group--append el-input-group--prepend el-input--suffix"
      >
        <div class="el-input-group__prepend">显示字段选项</div>
        <el-button-group size="medium">
          <el-checkbox-button v-model="checkAll" @change="checkedAll">
            全选
          </el-checkbox-button>
          <el-checkbox-button
            v-for="item in items.display"
            :label="item.value"
            :key="item.value"
            v-model="item.checked"
          >
            {{ item.text }}
          </el-checkbox-button>
        </el-button-group>
      </div>
    </el-row>

    <el-row style="margin-top: 15px;">
      <el-button native-type="submit" type="primary" size="medium">
        开始搜索
      </el-button>
    </el-row>
  </div>
</template>

<script>
import QueryText from "@/components/query/Text.vue";
import QueryNumber from "@/components/query/Number.vue";
import QuerySelect from "@/components/query/Select.vue";
import QueryDate from "@/components/query/Date.vue";

export default {
  name: "Query",
  data() {
    return {
      checkAll: false
    };
  },
  props: {
    items: Object
  },
  methods: {
    checkedAll(value) {
      this.checkAll = value;
      for (var i in this.items.display) {
        this.items.display[i].checked = this.checkAll;
      }
    }
  },
  components: {
    QueryText,
    QueryNumber,
    QuerySelect,
    QueryDate
  }
};
</script>
