<template>
  <div>
    <template v-for="(item, key) in items">
      <el-submenu v-if="item.url == ''" :key="key" :index="key + ''">
        <template slot="title">
          <i v-if="item.icon" style="color: #bfcbd9;" :class="item.icon"></i>
          <span slot="title">{{ item.name }}</span>
        </template>
        <submenu v-if="item.submenu" :items="item.submenu"></submenu>
      </el-submenu>
      <el-menu-item v-else :key="key" :index="'/index/' + item.url">
        <i v-if="item.icon" :class="item.icon"></i>
        <span slot="title" @mouseenter="enter(key)" @mouseleave="leave()">
          {{ item.name }}
          <el-button
            title="新页面打开"
            v-show="current == key"
            @click="openPage('#/index/' + item.url)"
            class="el-icon-link"
            circle
            size="mini"
            style="margin-left: 10px;"
          ></el-button>
        </span>
      </el-menu-item>
    </template>
  </div>
</template>

<script>
export default {
  name: "Submenu",
  data() {
    return {
      current: 999
    };
  },
  props: {
    items: Array,
    isMenuCollapse: Boolean
  },
  methods: {
    enter(index) {
      this.current = index;
    },
    leave() {
      this.current = 999;
    },
    openPage(url) {
      window.open(url, "_blank");
      return;
    }
  }
};
</script>

<style>
.el-menu--collapse .el-submenu__title span {
  display: none;
}
.el-menu--collapse .el-submenu__title .el-submenu__icon-arrow {
  display: none;
}
.el-menu {
  border: 0px;
}
.el-submenu__title {
  height: 40px;
  line-height: 40px;
}
.el-submenu .el-menu-item {
  height: 40px;
  line-height: 40px;
}
.el-menu-item {
  height: 40px;
  line-height: 40px;
}
</style>
